export default {
  name: "upload_file",
  props: {
    show: undefined
  },
  destroyed() {
    this.cancel_upload();
    console.log('call destroyed');
  },
  created() {
    this.upload_init();
    window._upload_file_dialog = this;
  },
  data() {
    return {
      call_cancel: false,
      orginal_title: document.title,
      video_upload_resumable: undefined,
      uploading_file_name: undefined,
      upload_progress: 0,
      browse_loading: false,
      upload_complete: false,
      browse_button_label: this.$l('videos.upload'),
      upload_token: undefined,
    }
  },
  watch: {
    show: function (newValue, oldValue) {
      this.$emit('update:show', newValue);
      console.log('call watch show change');
    }
  },
  methods: {
    on_click_close: function (e) {
      console.log('on_click_close:', e);
      this.call_cancel = true;
    },
    cancel_upload: function () {
      if (this.video_upload_resumable) {
        this.video_upload_resumable.cancel();
        this.call_cancel = true;
        this.set_title(this.orginal_title);
      }
    },
    on_hide: function () {
      console.log('call upload file dialog on hide');
      this.cancel_upload();
      this.$emit('upload_hide', {});
    },
    on_close_dialog: function () {

    },
    on_upload_cancel: function () {
      this.call_cancel = true;
      console.log('on_upload_cancel:',on_upload_cancel);
    },
    on_upload_error: function () {

    },
    on_upload_complete: function () {
      console.log('this.call_cancel:', this.call_cancel);
      if (!this.call_cancel) {
        console.log('call on_upload_complete');
        this.upload_complete = true;
        this.set_title(this.orginal_title);
        this.$emit('upload_complete', {
          'file_name': this.uploading_file_name,
          'token': this.upload_token
        });
        this.$q.notify({
          message: '已成功上传文件。',
          color: 'primary',
        });
        this.show = false;
      }
    },
    on_upload_progress: function () {
      this.upload_progress = this.video_upload_resumable.progress();
      var title = "(" + parseInt(this.upload_progress * 100) + "%)" + "正在上传";
      this.set_title(title);
      this.browse_loading = true;
    },
    on_add_upload_file: function (file, event) {
      console.log('fileAdded:', file, event);
      this.call_cancel = false;
      var file_size = this.video_upload_resumable.getSize();
      var file_size_mb = file_size / 1024 / 1024;
      if (file_size_mb > 1024 * 10) {
        return;
      }
      this.video_upload_resumable.upload();
      this.uploading_file_name = this.video_upload_resumable.files[0].fileName;
    },
    upload_init: function () {

      if (!window.Resumable) {
        console.log('no Resumable');
        return setTimeout(this.upload_init, 100);
      }
      if (!document.getElementById('browseButton')) {
        return setTimeout(this.upload_init, 100);
      }
      this.$axios.get('/app/upload/token').then((response) => {
        console.log('[upload token]response:', response);
        var nid = response.data.nid;
        var space_conf = this.$config.space_confs[nid];
        console.log('space_conf:', space_conf);
        var host = space_conf.static_hosts[0];
        var upload_target = 'https://' + host + '/upload';
        console.log('upload_target:', upload_target);
        this.upload_token = response.data.token;

        this.video_upload_resumable = new window.Resumable({
          target: upload_target,
          query: {
            token: this.upload_token,
            network: 'xx'
          }
        });

        this.video_upload_resumable.assignBrowse(document.getElementById('browseButton'));

        this.video_upload_resumable.on('fileAdded', this.on_add_upload_file);
        this.video_upload_resumable.on('filesAdded', this.on_add_upload_file);
        this.video_upload_resumable.on('progress', this.on_upload_progress);
        this.video_upload_resumable.on('error', this.on_upload_error);
        this.video_upload_resumable.on('complete', this.on_upload_complete);
        this.video_upload_resumable.on('cancel', this.on_upload_cancel);


      }).catch((error) => {
        console.log('[upload_file]error:', error);
        this.show = false;
        this.$q.notify({
          message: '获取上传文件凭证失败，请重试.',
          color: 'primary',
        });
        this.$emit('on_upload_error', {});
      });
    }
  }
}
