import upload_file from './upload_file';

export default {
  name: "attach_content",
  components: {
    upload_file
  },
  props: {
    show: undefined,
    buy_content: {
      price: 0,
      content: '',
      upload_files: []
    },
  },
  created() {
    console.log('create attach_content:', this.buy_content);
  },
  data() {
    return {
      show_upload_file_dialog: false,
    }
  },
  watch: {
    show: function (newValue, oldValue) {
      console.log('call watch show change');
      this.$emit('update:show', newValue);
    }
  },
  methods: {
    on_sure: function () {
      if (!this.buy_content.content || this.buy_content.content.length <= 1) {
        return this.$q.notify({
          message: '内空不能为空！',
          color: 'primary',
        });
      }

      if (this.buy_content.upload_files && this.buy_content.upload_files.length > 0) {
        for (var i = 0; i < this.buy_content.upload_files.length; ++i) {
          var upload_file = this.buy_content.upload_files[i];
          if (!upload_file.set_name) {
            return this.$q.notify({
              message: '请设置上传文件的显示名称！',
              color: 'primary',
            });
          }
        }
      }
      this.$emit('on_submit', this.buy_content);
      console.log('[on_sure]:', this.show);
      this.show = false;
      this.$emit('update:show', this.show);

    },
    on_close_dialog: function () {
      if (this.buy_content.upload_files && this.buy_content.upload_files.length > 0) {
        this.$q.dialog({
          title: '提示',
          message: '已有文件上传完成，是否确定关闭？',
          cancel: "取消",
          persistent: true,
          ok: "确定",
        }).onOk(() => {
          this.show = false;
        }).onCancel(() => {
        }).onDismiss(() => {
        })
      } else {
        this.show = false;
      }
    },
    on_upload_hide: function () {
      console.log('call on_upload_hide');
      this.show_upload_file_dialog = false;
    },
    on_upload_complete: function (upload_file) {
      console.log('on_upload_complete:', upload_file);
      for (var i = 0; i < this.buy_content.upload_files.length; ++i) {
        var tmp_upload_file = this.buy_content.upload_files[i];
        if (tmp_upload_file.token == upload_file.token) {
          console.log('重复:', upload_file);
          return false;
        }
      }
      this.buy_content.upload_files.push(upload_file);
      this.show_upload_file_dialog = false;
    },
    on_delete_upload_file: function (upload_file) {
      console.log('on_delete_upload_file:', upload_file);
      this.$q.dialog({
        title: '提示',
        message: '是否确定删除此上传文件？',
        cancel: "取消",
        persistent: true,
        ok: "确定",
      }).onOk(() => {
        this.buy_content.upload_files = this.buy_content.upload_files.filter(function (val) {
          return val.token != upload_file.token;
        });
        console.log('this.buy_content.upload_files:', this.buy_content.upload_files);
      }).onCancel(() => {
      }).onDismiss(() => {
      })
    },
    on_show_upload_file_dialog() {
      console.log('call on_show_upload_file_dialog');
      this.show_upload_file_dialog = false;
      this.$nextTick(() => {
        this.show_upload_file_dialog = true;
      });
      this.$forceUpdate();
    },
  }
}
