import '@/assets/css/quill/quill.snow.css'
import '../../assets/css/editor.css';
import forumobjct from "../script/forumobjct";
import attach_content from './items/attach_content';


var defalut_forum_conf = {
  'release': {
    'buy': true,
    'buy_theav': true,
    'buy_content': true,
    'setting': true,
    'update_file': false,
    'upload_video': false,
  }
};

var defalut_buy_content = {
  price: 0,
  content: '',
  upload_files: []
};

export default {
  name: "thread",
  mixins: [forumobjct],
  components: {
    attach_content
  },
  created() {

    this.show_types = ['videos'];
    window._t = this;
    if (!this.$store.state.hasLogin) {
      this.$q.dialog({
        title: this.$l('title_board_message'),
        message: '对不起，请先登录',
        cancel: false,
        ok: this.$l('login'),
        persistent: true
      }).onOk(data => {
        this.$router.push('/account/login');
      })

      var show_types = this.$route.query.show_types;

      if (show_types) {
        this.show_types = [];
        this.show_types.push(show_types);
      }

    }
    this.create_editor()

    this.forums_options = []
    this.update_forums_options(this.$forum_conf.forums, this.forums_options)
    var urlvars = this.getUrlVars();
    if (urlvars.fid) {
      var this_forum_conf = this.get_forum(urlvars.fid);
      console.log('this_forum_conf:', this_forum_conf);
      if (this_forum_conf) {
        this.on_select_forum(this_forum_conf);
      }
    }
    if (urlvars.edit_id) {
      this.edit_id = urlvars.edit_id;
    }
    console.log('forums_options:', this.forums_options);
  },
  data() {
    return {
      show_attach_content_dialog: false,
      show_node_dialog: false,
      editor_setting: {},
      loaded_quill: false,
      editor: undefined,
      title: "",
      release_loading: false,
      add_buy_content_open: false,
      add_buy_download_open: false,
      add_buy_theav_open: false,
      attach_file_upload: undefined,
      thread: {
        title: "",
        setting: {
          'not_reply': false
        },
        fid: undefined,
      },
      buy_download: {
        price: 20
      },
      buy_content: Object.assign({}, defalut_buy_content),
      buy_theav: {
        price: 100,
        id: ''
      },
      post_success: false,
      post_success_tip: "恭喜你，已成功提交，请等待审核通过。",
      edit_id: '',
      show_loading: false,
      forums_options: [],
      forums_options_disabled: false,
      this_forum_conf: Object.create(defalut_forum_conf),
      select_forum_conf: undefined,
      show_select_forum_text: undefined,
      post_response: undefined,
      buy_contents: [],
      show_types: ['videos']
    }
  },
  methods: {
    update_setting: function () {
      this.editor_setting.release = defalut_forum_conf.release;
      if (this.this_forum_conf.release) {
        this.editor_setting.release = this.this_forum_conf.release;
      }
      if (this.this_forum_conf.setting) {
        this.thread.setting = this.this_forum_conf.setting;
      }
      this.$forceUpdate();
    },
    on_dialog_select_forum: function (e) {
      this.on_select_forum(e);
      this.show_node_dialog = false;
      this.$forceUpdate();
    },
    on_select_forum: function (e) {
      this.select_forum_conf = this.this_forum_conf = e;
      var release = this.this_forum_conf.release;
      if (!release) {
        release = {};
      }
      this.this_forum_conf.release = Object.assign({}, defalut_forum_conf.release, release);

      var forum_list = [];
      this.show_select_forum_text = ""
      this.get_forum_list(this.select_forum_conf.fid, forum_list);
      for (var i = 0; i < forum_list.length; ++i) {
        this.show_select_forum_text += forum_list[i]['name'] + "/"
      }
      this.show_select_forum_text = this.show_select_forum_text.substr(0, this.show_select_forum_text.length - 1);
      this.thread.fid = this.this_forum_conf.fid;
      this.update_setting();
    },
    on_release: function () {
      var post_data = {
        'thread_inner_html': this.editor.root.innerHTML,
        'edit_id': this.edit_id,
        'thread': this.thread
      };
      if (this.buy_contents && this.buy_contents.length > 0) {
        post_data['buy_contents'] = this.buy_contents
      }
      if (this.add_buy_theav_open) {
        post_data['buy_theav'] = this.buy_theav
      }
      this.release_loading = true;

      this.$axios.post('/app/release/thread', post_data).then((response) => {
        this.release_loading = false;
        if (response) {
          this.post_success = true;
          this.post_response = response;
        }
        this.edit_id = response.data.id;
      }).catch((error) => {
        this.release_loading = false;
        this.$message("发布话题出错，请稍等再试或者联系我们。");
      });
    },
    onReset: function () {
    },
    create_forum_list: function () {

    },
    on_select_node: function () {
      this.show_node_dialog = false;
      this.show_node_dialog = true;
      this.$forceUpdate();
    },
    add_buy_theav: function () {
      this.add_buy_theav_open = !this.add_buy_theav_open;
    },
    show_edit: function () {
      if (this.edit_id) {
        console.log("show_edit:", this.edit_id);
        this.show_loading = true;
        this.get_topic({
          id: this.edit_id, from_cache: false, cb: (thread) => {
            this.thread = Object.assign(this.thread, thread);
            this.thread.title = thread.title;
            if (thread.setting) {
              this.thread.setting = thread.setting;
            }
            console.log('thread.content:', thread.content);
            this.editor.root.innerHTML = thread.content;
            console.log('thread.content2:');
            if (thread.buy_contents) {
              this.buy_contents = thread.buy_contents;
            }
            console.log('this.buy_contents:', this.buy_contents);

            if (thread.buy_theav) {
              this.buy_theav = Object.assign(this.buy_theav, thread.buy_theav);
              if (this.buy_theav.id) {
                this.add_buy_theav_open = true;
              }
              this.thread.forum = thread.fid;
              this.this_forum_conf = this.get_forum(thread.fid);
              this.on_select_forum(this.this_forum_conf);
            }
            this.$forceUpdate();
            this.show_loading = false;
          }
        })
      }
    },
    on_load_editor: function () {
      this.show_edit()
    },
    on_show_attach_content_dialog(buy_content) {
      console.log('buy_content:', buy_content)
      if (!buy_content) {
        buy_content = Object.assign({}, defalut_buy_content);
        buy_content.upload_files = [];
      }
      if (buy_content) {
        this.buy_content = buy_content;
      }
      this.show_attach_content_dialog = false;
      this.$nextTick(() => {
        this.show_attach_content_dialog = true;
        this.$forceUpdate();
      });
      this.$forceUpdate();
    },
    on_submit_buy_content: function (buy_content) {
      console.log('on_submit_buy_content:', buy_content);
      var is_add = true;
      for (let i = 0; i < this.buy_contents.length; ++i) {
        if (this.buy_contents[i] == buy_content) {
          is_add = false;
          break;
        }
      }
      if (is_add) {
        this.buy_contents.push(buy_content);
      }
      console.log('[on_submit_buy_content] is_add:', is_add);

      //this.show_attach_content_dialog = false;
      this.$forceUpdate();
    },
    create_editor: function () {

      console.log('call create_editor');

      var Quill = window.Quill;
      if (Quill && !this.loaded_quill) {
        this.loaded_quill = true;
      }
      var ImageResize = window.ImageResize;
      if (!Quill || !ImageResize) {
        return setTimeout(this.create_editor, 100);
      }

      var ele_editor = document.getElementById('editor');
      if (!ele_editor) {
        return setTimeout(this.create_editor, 100);
      }

      if (!window.htmlEditButton) {
        return setTimeout(this.create_editor, 100);
      }

      var toolbarOptions = [
        ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
        ['blockquote', 'code-block'],

        [{'direction': 'rtl'}],                         // text direction
        [{'header': [1, 2, 3, 4, 5, 6, false]}],

        [{'color': []}, {'background': []}],

        [{'align': []}],
        ['link', 'image'],
        ['clean']                                         // remove formatting button
      ];

      try {

        Quill.register("modules/htmlEditButton", window.htmlEditButton);

        let Block = Quill.import('blots/block');

        class HeaderBlot extends Block {
          static formats(node) {
            return HeaderBlot.tagName.indexOf(node.tagName) + 1;
          }
        }

        HeaderBlot.blotName = 'header';
        HeaderBlot.tagName = ['H1', 'H2'];
        Quill.register(HeaderBlot);


        class Div extends Block {
        }

        Div.tagName = "div";
        Div.blotName = "div";
        Div.allowedChildren = Block.allowedChildren;
        Div.allowedChildren.push(Block)
        Quill.register(Div);

        let BlockEmbed = Quill.import('blots/block/embed');

        class VideoBlot extends BlockEmbed {
          static create(url) {
            let node = super.create();
            node.setAttribute('src', url);
            node.setAttribute('frameborder', '0');
            node.setAttribute('allowfullscreen', true);
            return node;
          }

          static formats(node) {
            let format = {};
            if (node.hasAttribute('height')) {
              format.height = node.getAttribute('height');
            }
            if (node.hasAttribute('width')) {
              format.width = node.getAttribute('width');
            }
            return format;
          }

          static value(node) {
            return node.getAttribute('src');
          }

          format(name, value) {
            if (name === 'height' || name === 'width') {
              if (value) {
                this.domNode.setAttribute(name, value);
              } else {
                this.domNode.removeAttribute(name, value);
              }
            } else {
              super.format(name, value);
            }
          }
        }

        VideoBlot.blotName = 'video';
        VideoBlot.tagName = 'iframe';

        Quill.register(VideoBlot);

        try {
          this.editor = new Quill('#editor', {
            modules: {
              toolbar: toolbarOptions,
              htmlEditButton: {
                msg: "修改HTML源码", //Custom message to display in the editor, default: Edit HTML here, when you click "OK" the quill editor's contents will be replaced
                okText: "确定", // Text to display in the OK button, default: Ok,
                cancelText: "取消", // Text to display in the cancel button, default: Cancel
                buttonHTML: "&lt;&gt;", // Text to display in the toolbar button, default: <>
                buttonTitle: "显示HTML源码", // Text to display as the tooltip for the toolbar button, default: Show HTML source
                syntax: false
              },
              imageResize: {
                displayStyles: {
                  backgroundColor: 'black',
                  border: 'none',
                  color: 'white',
                },
                displaySize: true,
                //modules: ['Resize', 'DisplaySize', 'Toolbar']
              }
            },
            theme: 'snow',
          });
        } catch (e) {
          console.error('[new Quill]e:', e);
          location.reload();
          //return setTimeout(this.create_editor, 100);
        }

        this.on_load_editor();
      } catch (e) {
        console.log('create editor error:', e);
      }
    }
  }
}
